@media (min-width: 768px) {
    .event-details {
        margin: 0 auto;
        padding-top: 32px;
        width: 900px;

        .event-details-thumbnail {
            display: inline-block;
            max-width: 400px;
            vertical-align: top;
            width: 40%;
        }

        .event-details-wrapper {
            display: inline-block;
            width: 60%;
            padding: 16px;

            .event-details-title {
                font-size: 1.5rem;
                font-weight: 700;
                padding: 0 16px;
            }

            .event-details-desc {
                font-size: 1rem;
                font-weight: 400;
                padding: 0 16px;

                .title {
                    font-size: 1rem;
                    color: #000;
                }

                .event-details-link {
                    text-decoration: underline;
                    padding: 0;
                }
            }

            .event-details-link {
                text-decoration: underline;
                padding: 0 16px;
            }
        }

        .event-details-cards {
            margin: 0 16px 10px;
            background-color: white;
            border-radius: 25px;
            padding:24px 16px;
            .title{
                font-size: 1.3rem;
                font-weight: 400;
            }
            .item {
                padding-top: 16px;
                vertical-align: top;

                .icon {
                    display: inline-block;
                    padding-top: 5px;
                    vertical-align: top;
                    width: 30px;
                }

                .info {
                    color: #2c2c2c;
                    display: inline-block;
                    font-size: 0.875rem;
                    font-weight: 400;
                }

                .info warning {
                    color: red;
                }
            }
        }
    }
}

@media (max-width: 768px) {

    .event-details {

        .event-details-thumbnail {
            display: block;
            overflow-wrap: break-word;
        }

        .event-details-wrapper {

            .event-details-title {
                font-size: 1.5rem;
                font-weight: 700;
                padding: 24px 16px 8px;
            }

            .event-details-desc {
                font-size: 1rem;
                font-weight: 400;
                padding: 0 16px;

                .title {
                    font-size: 1rem;
                    color: #000;
                }

                .event-details-link {
                    text-decoration: underline;
                    padding: 0;
                }
            }

            .event-details-link {
                text-decoration: underline;
                padding: 0 16px;
            }
        }
        .event-details-cards {
            margin: 0 16px 10px;
            background-color: white;
            border-radius: 25px;
            padding:24px 16px;
            .title{
                font-size: 1.3rem;
                font-weight: 400;
            }
            .item {
                padding-top: 16px;
                vertical-align: top;

                .icon {
                    display: inline-block;
                    padding-top: 5px;
                    vertical-align: top;
                    width: 30px;
                }

                .info {
                    color: #2c2c2c;
                    display: inline-block;
                    font-size: 0.875rem;
                    font-weight: 400;
                }

                .info warning {
                    color: red;
                }
            }
        }
    }
}